html{
  padding: 3px;
  background-color: #00006e;
  overflow:hidden;
}
.lunaP{
  
  display: flex;
  justify-content: center;
}
.luna{
  height: 100vh;
  width: 80vw;
  margin: 0%, 10%, 0, 10%;
  padding: 0%, 10%;
  top:0%;
  bottom:0%;

  background-color: #00006e
  /*overflow: hidden; */
}


#tsparticles {
  width: 100%;
  height:100%;
  
  
  margin: 0;
  padding: 0;
  
}

.imglu{
position:absolute;
top: 15px;
margin-left: 10px;
  
}

.tablaPeri{
  position:absolute;
  
  width:100%;
  height: 98%;
  
}
#tablet{
  position:absolute;
  top: 15%;
  right:0%;
  left:0%;
  display: flex;
 justify-content: center;
 margin: 0%;
 padding: 0%;
  
  
}
td {
  width: 4vw;
  height: 6.8vh;
    
  }
td:hover{
  
  filter:brightness(60%);
  cursor:pointer; 
}




.centro{
  border-style: solid;
  border-width: 0.5px;
  padding: 0%;
  cursor:none;
  pointer-events: none;
  border-radius: 20px;
}



.general{
  border-style: solid;
  border-width: 0.5px;
  
  padding: 0%;
  
  

}





.general h2{
    text-align: center;
    vertical-align: center;
    position:relative;
    top: 5%;
    font-size: 20px;
    margin:0%;
    border-width: 0px;
    
}
.general h3{
  text-align: center;
  vertical-align: top;
  
  
  font-size: 9px;
  margin:0%;
  border-width: 0px;
  

}

.numero{
  
  border-style: none;
  text-align: center;
  
  vertical-align: bottom;
  color:white;
  pointer-events: none;
}
.numero2{
  border-style: none;
  text-align: right;
  vertical-align: center;
  color:white;
  pointer-events: none;
}


.ndiv{
  height: 100%;
  border-width: 0;
  
  
  
}

.general h4{
  display:flex;
  justify-content: center;
  font-size: 5px;
  margin:0%;
  margin-top: 1px; 

}
.general h5{
  
  
  font-size: 8px;
  margin:0%;
  
  }


.divg{
  padding:0%;
  margin: 0%;
  
  border-style: none;
  height: 100%;

}
.div1{
  
  height: 70%;
  
}
.div2{
  height: 30%;
  width: 100%;
  
  

}

.space {
  position:relative;
  width: 100%;
  
  display: flexbox;
  justify-content: center;
  margin: 0%;
  font-size: 5px;
  padding: 0%;
  
}

.oculto{
  visibility: hidden;
  pointer-events: none;
}
#logoP{
  position: absolute;
  place-content: right;
  display:flex;
  bottom: 0%;
  right: 0%;
  text-align: left;
}
#logoP p{
  font-size:1.6vw;
  margin: 0%;
  color:white;
}
.logo2i{
  padding-inline: 1vw;
  width: 9vw;
  margin: 0%;
}
.logo3i{
  padding-inline: 0.5vw;
  width: 8vw;
  margin: 0%;
}

@media screen and (min-width: 768px) {
  
  #ttable table{
    margin:0%;
    border-spacing: 0%;
    overflow:hidden;
  }
 
  .thead{  /* MEDIDAS LAPTOP */ 
    background-color: #42019e;
    font-size: 1.7vw;
    font-style: italic;
    height: 3vh;
    padding-inline: 10px;
    color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    padding-block: 2px;
  }
  .tinfo{
    font-size: 12px;
    padding-inline: 10px;
    padding-block: 2px;
    height: 4vh;
    background-color: #42019e;
    display:flex;
    align-items: center;
    color: white;
    
  }
  .tinfo p{
    margin: 0;
    
  }
  .tinfo p{
    margin: 0;
    
  }
  .tsize{   /* MEDIDAS LAPTOP */ 
    width: 1.2vw;
    height: 2vh;
    border-style: solid;
    border-width: 0.5px;
    
  }
  .tsize1{
    width: 1.5vw;
    height: 1vh;
    align-items: right;
    padding: 0%;
    
  }
  .tsize2{
    width: 12vw;
    text-align: left;
    font-size: 12px;
    height: 0.4vh;
    padding: 0%;
  }
  #ttable{
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: #F5F5F5;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    
    
  }
}
