.big{ /*big bang */
    color: white;
    
    background-color:#303F9F ;
}
.esp { /* estrellas pequeñas */
    -webkit-text-stroke: 1.2px black;
    color:white;
    background-color: #009688;
}
.esg{  /*estrellas grandes */   
    color: white;
    background-color: #F44336;
}
.suno { /*super novas*/
    background-color: #dce775;
}
.nona{ /*no naturales*/
    color: white;
    background-color: #9C27B0;
}
.ray{ /*rayos y estrellas pequeñas */
 background-color: #FFEB3B;
}
.raes{  /*rayos y estrellas pequeñas */
    
    background-image: -webkit-linear-gradient(-50deg, #FFEB3B 50%, #009688 50%);
}
.raes2{  /*rayos y estrellas pequeñas */
    
    color: white;
    background-color: #42019e;
}
.epeg { /*estrellas pequeñas y estrellas grandes*/
    background-image: -webkit-linear-gradient(-50deg,#009688 50%, #F44336 50%);
}
.egsn{  /*estrellas grandes y supernovas*/
    
    background-image: -webkit-linear-gradient(-50deg, #F44336 50%, #dce775 50%);
}
.epsn{  /*estrellas pequeñas y supernovas*/
    
    background-image: -webkit-linear-gradient(-50deg, #009688 50%, #dce775 50%);
}

.epnn { /*estrellas pequeñas y no naturales */
    color: white;
    background-image: -webkit-linear-gradient(-50deg, #009688 50%, #9C27B0 50%);
}

