
.Popup {
    font-family: sans-serif;
    text-align: center;
  }
  .Popup button {
    padding: 13px 29px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    border: none;
    outline: none;
    background: crimson;
    color: white;
    cursor: pointer;
  }
  #portal .container {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.7);
  }
  .modal {
    z-index: 1;
    width: 50vw;
    height: 50vh;
    background-color: rgba(0, 0, 0, 255);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    position: relative;
    animation: animate 0.3s;
    border-radius: 30px;
  }
  .modal button {
    position: absolute;
    top: -120px;
    right: 33px;
    padding: 8px 11px;
    background: rgb(36, 1, 134);
    color: white;
    font-weight: bold;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 25%;
  }
  .modal img{
    width: 43vw;
  }

  .modBo{
    height: 80%;
    display:flex;
    justify-content:space-evenly;
    align-items: center;

  }

  .SecMo{
    height: 100%;
    width: 100%;
  }
  .modHe{
    display:flex;
    width: 100%;
    height: 5vh;
    font-size: x-large;
    font-weight: bold;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

    
    
  }
  .modHe h3{
    margin: 0%;

  }
  .ele{
    border: 1px solid black;
    height: 15.5vh;
    width: 7vw;
    margin: 0%;

  }
  .ele h5 h4 h1{
    margin:0;

  }
  .ele h5{
    position: relative;
    vertical-align: bottom;
    text-align: right;
    margin:0%;
    font-size: 1.2vw;
    right: 6px;
    
  }
  .ele h1{
    display:flex;
    position:relative;
    margin:0%;
    justify-content: center;
    align-items: center;
    bottom: 1.5vh;
    font-size: 4.3vw;
    
  }
  .ele h4{
    position: relative;
    margin:0%;
    text-align: center;
    bottom: 1.2vw;

  }
  
  #tableMo td, #tableMo tr{
    width: 18vw;
    border: 1px solid;
    padding: 0px 5px;
    height: 1vh;
  }
  #tableMo tr:nth-child(even){background-color: #c8e0ff;}
  #tableMo {
    padding:0%;
    border-collapse: collapse;
  }
  .modBo img{
    width: 8vw;
    vertical-align: center;
  }
  #tableMo tr:hover{
    filter: brightness(60%);
  }
  
  
  
  
  
  
  
  
  


  @media screen and (min-width: 768px) {
  
    .container .modal {
      z-index: 1;
      width: 50vw;
      height: 50vh;
      background: transparent;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
      position: relative;
      animation: animate 0.3s;
      border-radius: 30px;
    }
    
    .modal button {
      position: absolute;
      top: -17.5vh;
      right: 33px;
      padding: 8px 11px;
      background: rgb(36, 1, 134);
      color: white;
      font-weight: bold;
      border: none;
      outline: none;
      cursor: pointer;
      border-radius: 25%;
    }

    #tableMo td{
      font-size: 12px;
    }
    #tableMo td span{
      font-size: 14px;
    }


    .ele{
      border: 1px solid black;
      height: 15.5vh;
      width: 7vw;
      margin: 0%;
    
    }
    .ele h5 h4 h1{
      margin:0;
    
    }
    .ele h5{
      position: relative;
      vertical-align: bottom;
      text-align: right;
      margin:0%;
      font-size: 1.2vw;
      right: 6px;
      
    }
    .ele h1{
      display:flex;
      position:relative;
      margin:0%;
      justify-content: center;
      align-items: center;
      bottom: 1.5vh;
      font-size: 4.3vw;
      
    }
    .ele h4{
      position: relative;
      margin:0%;
      text-align: center;
      bottom: 1.2vw;
    
    }
      
    #tableMo td, #tableMo tr{
      width: 20vw;
      border: 1px solid;
      padding: 0px 5px;
      height: 1vh;
    }
    #tableMo tr:nth-child(even){background-color: #c8e0ff;}
    #tableMo {
      padding:0%;
      border-collapse: collapse;
    }
    .modBo img{
      width: 8vw;
    }
    #tableMo tr:hover{
      filter: brightness(60%);
    }

  }
  @media screen and (min-width: 1281px) {
    #tableMo td{
      font-size: 17px;
    }
    #tableMo td span{
      font-size: 18px;
    }
    .modal button {
      position: absolute;
      top: -20vh;
      right: 3vw;
      padding: 8px 11px;
      background: rgb(36, 1, 134);
      color: white;
      font-weight: bold;
      border: none;
      outline: none;
      cursor: pointer;
      border-radius: 25%;
    }
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  @keyframes animate {
    from {
      transform: scale(0.5);
    }
    to {
      transform: scale(1);
    }
  }